<template>
  <v-card transition="scroll-y-transition">
    <v-toolbar color="rgba(39,55,70,1)" dark flat elevate-on-scroll>
      <v-btn icon @click="back()">
        <v-icon size="23" color="#D5DBDB">arrow_back</v-icon>
      </v-btn>

      <v-toolbar-title>Dictamen</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>

      <template v-slot:extension>
        <v-tabs v-model="tab" left>
          <v-tabs-slider color="yellow"></v-tabs-slider>
          <v-tab>
            <v-icon>person</v-icon>
          </v-tab>
          <v-tab v-for="(item, i) in items" :key="i">
            {{ item.tab }}
          </v-tab>
        </v-tabs>
      </template>
    </v-toolbar>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <v-card flat>
          <client-detail />
        </v-card>
      </v-tab-item>
      <v-tab-item v-for="(item, i) in items" :key="i">
        <component v-bind:is="item.content"></component>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>
<script>
export default {
  components: {
    ClientDetail: () => import("@/components/sections/client/ClientDetail.vue"),
    BoardList: () => import("@/components/sections/board/BoardList.vue"),
  },
  data() {
    return {
      tab: 0,
      items: [
        {
          tab: "Tableros",
          content: "BoardList",
        },
      ],
    };
  },
  computed: {
    section() {
      return this.$store.getters.getSection;
    },
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      this.section.forEach((element) => {
        this.items.push(element);
      });
    },
    back() {
      this.$router.push("/");
    },
  },
};
</script>